import {bindable} from 'aurelia-framework';

export class Dropdown {
  @bindable options = [];
  @bindable isOpen = false;
  @bindable prompt = 'Select item';
  @bindable current = '';

  toggleOpen() {
    this.isOpen = !this.isOpen;
  }

  selectItem(selected) {
    this.current = selected.name;
    this.isOpen = false;
  }

  clearSelected() {
    this.current = '';
  }
}
